// External Dependencies
import React from 'react';
import PropTypes from 'prop-types';
import database from '../firebase/firebase';

// Internal Dependencies
import SignupContainer from './SignupContainer';

class SignupRetriever extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      preRenderFetchComplete: false
    };
    if (this.props.production && !this.props.formID) {
      throw new Error('In production mode, Nove signup form requires a formID in order to retrieve form settings.')
    }
  }
  componentDidMount() {
    const { userID, formID, production } = this.props;
    const startPreRenderFetch = (production && this.props.formID)
      ?
      this.preRenderFetch
      :
      () => Promise.resolve({}); // not production
    startPreRenderFetch({ userID, formID })
      .then((updates) => {
        this.setState({
          ...this.state,
          preRenderFetchComplete: true,
          ...updates
        });
      })
      .catch((err) => console.error(err));
  }
  // componentWillReceiveProps = nextProps => console.log(nextProps)
  preRenderFetch = ({ userID, formID }) => {
    const contactFormLocation = (`users/${userID}/contactForms/${formID}`);
    return database.ref(contactFormLocation).once('value')
      .then((snapshot) => {
        return Promise.resolve(snapshot.val());
      });
  }
  render() {
    // strip off props that aren't necessary for SignupContainer to know about
    const { preRenderFetchComplete, name, ...state } = this.state;
    const { production, ...props } = this.props;
    if ((this.props.production && this.state.preRenderFetchComplete) ||
        !this.props.production) {
      // console.log({ ...state, ...props });
      return (
        <SignupContainer
          {...props}
          {...state}
        />
      );
    }
    return (
      <div className="Signup Signup--loading">
        <div className="F">
          <div className="F__g F__g--dummy" />
          <div className="F__g F__g--dummy" />
        </div>
      </div>
    );
  }
}
SignupRetriever.propTypes = {
  formID: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.string
  ]),
  production: PropTypes.bool,
  userID: PropTypes.string.isRequired
};
SignupRetriever.defaultProps = {
  production: true,
  formID: false
};

export default SignupRetriever;
