import React from 'react';
import PropTypes from 'prop-types';
import DatePicker from 'react-datepicker';
import { css } from 'glamor';
import InputText from './InputText';
import InputSelect from './InputSelect';
import InputTextarea from './InputTextarea';
import { getStyles } from '../services/css';

const Signup = (props) => {
  // this function turns the input objects into JSX. used in both map functions below
  const getInputElements = ({
    id,
    hidden = false,
    required,
    label,
    placeholder,
    type,
    options
  }, i) => {
    let Input = InputText;
    let dynamicOnChange = props.onInputChange;
    let dynamicOnBlur = () => {};
    if (type === 'select') {
      Input = InputSelect;
    } else if (type === 'textarea') {
      Input = InputTextarea;
    } else if (type === 'phone') {
      dynamicOnBlur = props.onPhoneBlur;
    } else if (type === 'email') {
      dynamicOnBlur = props.onEmailBlur;
    } else if (type === 'currency') {
      dynamicOnChange = props.onAmountChange;
    } else if (type === 'date') {
      // perhaps I should just return the entire datepicker right here
      dynamicOnChange = props.onDateChange;
      return (
        <div className={`F__g F__g--${id}`} key={i}>
          {
            label &&
            <label className={`F__l${required ? ' F__l--req' : ''}`} htmlFor="eventDate">
              <span>{label}</span>
            </label>
          }
          <DatePicker
            id="eventDate"
            className={`F__i${(required && props.formError && props.eventDate === undefined) ? ' F__i--err':''}`}
            // this is handled above
            // error={ -~-~-~ ><H*> }
            valueRequired={required}
            selected={props.eventDate}
            placeholderText={placeholder}
            onChange={props.onDateChange}
            withPortal={props.windowWidth < 768}
            readOnly={props.windowWidth < 768}
            autoComplete="off"
            // popperPlacement={props.windowWidth < 668 ? 'right' : 'auto'}
            // popperModifiers={{
              // offset: {
                // enabled: true,
                // offset: props.windowWidth < 668 ? '0px, -30px' : ''
              // }
            // }}
          />
        </div>
      );
    }
    return (
      <Input
        style={({ ...(hidden ? { display: 'none' } : {}) })}
        key={i}
        classes={`F__g--${id}`}
        error={required && props.formError !== '' && props[id] === ''}
        id={id}
        label={label}
        onChange={dynamicOnChange}
        onBlur={dynamicOnBlur}
        options={options ? [['', placeholder], ...options] : false}
        placeholder={placeholder}
        value={props[id]}
        valueRequired={required}
      />
    );
  };
  // this getStyles actually creates the 'markup' for CSS in JS
  const style = getStyles(props.styles, props.order);
  const errorClasses = [
    ...(props.formError ? ['F__E--form'] : []),
    ...(props.emailError ? ['F__E--email'] : []),
    ...(props.phoneError ? ['F__E--phone'] : [])
  ];
  const drawer = props.inputs.outer.find(({ id }) => id === 'drawer') || {};
  return (
    <div className={`Signup ${props.classes ? props.classes : ''}${props.order ? ' Signup--custom-order' : ''}`} {...css(style.signup)}>
      <div className={`Signup__success${props.signupSuccess ? ' Signup__success--vis' : ''}`}>
        {
          props.signupSuccess &&
          <div className="Signup__success--txt">
            <h2 className="Signup__success-head h4">{props.signupSuccessHeading}</h2>
            <p className="Signup__success-msg h4">{props.signupSuccessMessage}</p>
            <button
              {...css(style.submit)}
              className="Signup__success-b B B--p"
              onClick={props.onSignupComplete}
            >
              {props.signupSuccessButton}
            </button>
          </div>
        }
      </div>
      {
        <div className={`F__E${errorClasses.length > 0 ? ` F__E--active ${errorClasses.join(' ')} F__E--${errorClasses.length}` : ''}`}>
          {props.formError && <p>{props.formError}</p>}
          {props.emailError && <p>{props.emailError}</p>}
          {props.phoneError && <p>{props.phoneError}</p>}
        </div>
      }
      {
        // almost all configs have an order, so this is almost always true
        props.inputs.orderedInputs ? (
          // output for when a custom input order is provided
          <form className="F F--custom-order" onSubmit={props.onSubmit}>
            {
              // inputs that are not specified as being in 'the drawer'
              props.inputs.orderedInputs.map((input, index) => {
                // this is an array, which makes it the drawer
                if (input.map) {
                  if (input.length > 0) {
                    return (
                      <div key="drawer" className="F__g F__g--more">
                        {
                          drawer.label &&
                          <label className="F__l"><span>{drawer.label}</span></label>
                        }
                        <div className={`Acrd${props.accordionOpen ? ' Acrd--o' : ''}`}>
                          <button onClick={props.onAccordionClick} className="Acrd__trg">
                            <span className="F__l">
                              <span id="drawer_placeholder">{drawer.placeholder}</span>
                            </span>
                          </button>
                          <div
                            className="Acrd__cont-c"
                            // duplicated below
                            style={
                              {
                                maxHeight: (props.accordionOpen && input.length > 0) ? `${(input.length * 110) + 24}px` : 0
                              }
                            }
                          >
                            <div className="Acrd__cont">
                              {
                                props.inputs.inner.map((innerInput, i) => getInputElements(innerInput, i))
                              }
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  } else { return; }
                }
                return getInputElements(input, index);
              })
            }
            <div className="F__g F__g--submit">
              <button className="B B--p F__sb" {...css(style.submit)}>{props.submitText || 'Submit'}</button>
            </div>
          </form>
        ) : (
          // output for a standard input order
          <form className="F" onSubmit={props.onSubmit}>
            {
              // inputs that are not specified as being in 'the drawer'
              props.inputs.outer.length > 0 &&
              props.inputs.outer.map((input, index) => getInputElements(input, index))
            }
            {
              // inputs that are specified as being in 'the drawer'
              props.inputs.inner.length > 0 &&
              <div className="F__g F__g--more">
                {
                  drawer.label &&
                  <label className="F__l"><span>{drawer.label}</span></label>
                }
                <div className={`Acrd${props.accordionOpen ? ' Acrd--o' : ''}`}>
                  <button onClick={props.onAccordionClick} className="Acrd__trg">
                    <span className="F__l">
                    <span id="drawer_placeholder">{drawer.placeholder}</span>
                    </span>
                  </button>
                  <div
                    className="Acrd__cont-c"
                    style={
                      {
                        // more or less duplicated above
                        maxHeight: props.accordionOpen ? `${(props.inputs.inner.length * 80) + 24}px` : 0
                      }
                    }
                  >
                    <div className="Acrd__cont">
                      {
                        props.inputs.inner.map((input, index) => getInputElements(input, index))
                      }
                    </div>
                  </div>
                </div>
              </div>
            }
            <div className="F__g F__g--submit">
              <button className="B B--p F__sb" {...css(style.submit)}>{props.submitText || 'Submit'}</button>
            </div>
          </form>
        )
      }
    </div>
  );
};

Signup.propTypes = {
  accordionOpen: PropTypes.bool.isRequired,
  classes: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  emailError: PropTypes.string.isRequired,
  eventDate: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.string
  ]),
  formError: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  inputs: PropTypes.object,
  styles: PropTypes.shape({
    backgroundColor: PropTypes.string,
    borderColor: PropTypes.string,
    drawerColor: PropTypes.string,
    drawerColorBg: PropTypes.string,
    labelColor: PropTypes.string,
    negativeColor: PropTypes.string,
    negativeColorBg: PropTypes.string,
    placeholderColor: PropTypes.string,
    positiveColor: PropTypes.string,
    positiveColorHover: PropTypes.string,
    textColor: PropTypes.string,
    width: PropTypes.string
  }).isRequired,
  order: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.array
  ]),
  onAccordionClick: PropTypes.func.isRequired,
  onAmountChange: PropTypes.func.isRequired,
  onDateChange: PropTypes.func.isRequired,
  onEmailBlur: PropTypes.func.isRequired,
  onPhoneBlur: PropTypes.func.isRequired,
  onSignupComplete: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  phoneError: PropTypes.string.isRequired,
  submitText: PropTypes.string,
  signupSuccess: PropTypes.bool.isRequired,
  signupSuccessButton: PropTypes.string.isRequired,
  signupSuccessHeading: PropTypes.string.isRequired,
  signupSuccessMessage: PropTypes.string.isRequired,
  onInputChange: PropTypes.func.isRequired,
  windowWidth: PropTypes.number.isRequired
};
Signup.defaultProps = {
  inputs: {},
  eventDate: undefined,
  order: false,
  submitText: undefined
};

export default Signup;
