import defaults, { eventTypes } from '../fixtures/forms';
import { shadeColor } from '../utils/utils';

export const getInputs = (custom = {}, types, order = false) => {
  const defaultKeys = Object.keys(defaults);
  const nonDefaultKeys = Object.keys(custom).filter(it => !defaultKeys.includes(it));
  // create a list of all keys (default plus custom) without any repeats
  const all = [
    ...defaultKeys,
    // only add keys that are not already included
    ...nonDefaultKeys
  ];

  // used to set required and active attributes.
  // forces name and email to be required and active
  const setRequiredDefaults = ({ defaults, custom, id, key }) => {
    // start with false just in case required isn't included in defaults and is omitted in custom
    let attribute = false;
    // these are always active and required
    if (['name', 'email'].includes(id)) {
      attribute = true;
      // console.warn(`Form field ${id} must always be ${key}.`);
    // use config for any keys that aren't in the array above
    } else {
      // set required to whatever the default state is if there is one
      if (defaults[id] && typeof defaults[id][key] === 'boolean') {
        attribute = defaults[id][key];
      }
      // set required to whatever the custom state is if there is one
      if (custom[id] && typeof custom[id][key] === 'boolean') {
        attribute = custom[id][key];
      }
    }
    return attribute;
  };

  const allInputs = all
    // build array from all keys with defaults overridden with any custom attributes
    .map((id) => {
      const index = order ? order.indexOf(id) : -1;
      const input = {
        id,
        ...defaults[id],
        ...custom[id],
        // this sets index if it exists. if not, it sets default if it exists. if not, set 0
        index: index !== -1 ? index : (defaults[id] ? defaults[id].index : 0),
        required: setRequiredDefaults({ defaults, custom, id, key: 'required' }),
        active: setRequiredDefaults({ defaults, custom, id, key: 'active' })
      };
      // set types to config types instead of defaults
      if (id === 'type' && types) { input.options = types; }
      return (input);
    })
    // filtered by whether the field is active [visible] or not
    .filter(field => field.active)
    // set values if default value is supplied
    .map((field) => {
      let { value = '' } = field;
      if (field.type === 'date') { value = undefined; }
      return ({
        ...field,
        value
      })
    })
    .sort((a, b) => a.index > b.index ? 1 : -1);


    const outer = allInputs.filter(it => !it.drawer);
    const inner = allInputs.filter(it => it.drawer);

  // handle piecing together inputs for custom order
  let orderedInputs = false;
  if (order) {
    orderedInputs = [];
    order.forEach((ID) => {
      if (ID === 'drawer') {
        return orderedInputs.push(inner);
      }
      let id = ID;
      // order array element is object when it is a custom user-generated form element
      if (typeof ID === 'object') {
        // pull the id attribute off of the object (which is unfortunately called ID)
        ({ id } = ID);
      }
      // issue 2: sometimes the order prop is not updated and this won't find an input... I dont understand
      const theInput = allInputs.find(({ id: inputID }) => id === inputID) || false;
      if (theInput && !theInput.drawer) {
        orderedInputs.push(theInput);
      }
    });
  }
  const retVal = ({
    // inputs that are on the top level of the form
    outer,
    // inputs that are in the drawer
    inner,
    orderedInputs,
    allInputs
  });
  return retVal;
};

export const getStyles = ({
  borderStyle,
  borderColor,
  drawerBackgroundColor,
  drawerTextColor,
  inputBackgroundColor,
  inputTextColor,
  labelTextColor,
  negativeColor,
  positiveColor,
  placeholderColor,
  width
} = {}) => ({
  borderStyle: ['full', 'underline', 'none'].includes(borderStyle) ? borderStyle : 'full',
  borderColor: borderColor || '#b3b3b3',
  drawerBackgroundColor: drawerBackgroundColor || '#eeeeee',
  drawerTextColor: drawerTextColor || '#707070',
  inputBackgroundColor: inputBackgroundColor || '#fefefe',
  inputTextColor: inputTextColor || '#444444',
  labelTextColor: labelTextColor || '#444444',
  negativeColor: negativeColor || '#e74c3c',
  negativeColorBg: shadeColor(negativeColor || '#e74c3c', 0.8),
  positiveColor: positiveColor || '#2ecc71',
  positiveColorHover: shadeColor(positiveColor || '#2ecc71', -0.09),
  placeholderColor: placeholderColor || '#b3b3b3',
  width: width || '560px'
});
