import moment from 'moment';
import { uuid } from './utils';

// used in AccountForm to format dates correctly or provide empty string to avoid Firebase fail
export const dateIfPresent = date => date !== '' ? moment(date).valueOf() : '';

export const formatFormInput = ({ id: ID, inputs }) => {
  const id = ID !== '' ? ID : uuid();
  const createdAt = moment().utcOffset(0).valueOf();
  // use existing id if it exists, otherwise create a new one based on name and timestamp
  const processedInputs = {};
  inputs.forEach(({ id, type, value: VALUE }) => {
    let value = VALUE;
    if (type === 'currency') {
      // currency data is stored in pennies
      value = VALUE * 100;
    } else if (type === 'date') {
      // event date data is stored in miliseconds, and always set to beginning of the day
      value = moment(VALUE).utcOffset(0).startOf('day').valueOf();
    }
    processedInputs[id] = value;
  });
  const returnValue = ({ createdAt, id, ...processedInputs });
  return returnValue;
};
